// Extracts a node_map index value from the span indices
// Inputs: indices of span values (as int)
// Output: Int (node map index) (returns -1 if the indices are not valid / identifiable in the string)
function getNodeMapIndex(left, right) {
    var temp = string_output.indexOf("span " + left + " " + right);

    if (temp == -1) {
        return -1;
    }

    console.log("Node map output");
    temp = string_output.substring(temp-25, temp-19);
    // replaces non numeric
    temp = temp.replace(/[^\d.-]/g, '');

    // console.log(temp);
    return parseInt(temp);
}

