// Input actual node
// Return function or siblings function
function getFunction(node) {
    // Get's node's function
    try {
    var tempIndex = node["name"].indexOf("rel2par"); }
    catch(error) {
        return "Elaboration";
    }
    var nodeFunction = node["name"].substring(tempIndex+8,node["name"].length-1);

    if (nodeFunction != "span") {
        return nodeFunction;
    }

    else {
        sibling = node_map[node["sibling"]];
        tempIndex = sibling["name"].indexOf("rel2par");
        nodeFunction = sibling["name"].substring(tempIndex+8,sibling["name"].length-1);
        return nodeFunction;
    }
    return nodeFunction;
}


