// Given a node name, extract the indices
function getIndices(name) {
    console.log("getIndices function")

    console.log(name);
    name = name.substring(name.indexOf("span") + 4, name.indexOf(")"));
    var bounds = name.trim().split(" ");
    var start_index = parseInt(bounds[0]);
    var end_index = parseInt(bounds[1]);
    return [start_index, end_index];
}

// Given leaf name, extract the number value
function getLeafNumber(name) {
    name = name.substring(name.indexOf("leaf") + 4, name.indexOf(")"));
    return(parseInt(name.trim()));
}

function getLeafTextIndices(a, b) {
    var output = "";
    for (var i = a; i <= b; i++) {
        output += " " + leaves_with_id[i]["text"];
    }
    // console.log(output.trim());
    console.log(a, b);
    return removeSpaceBefore(output.trim(), [".", ",", ";"]);
    // return output.trim();
}

// input node
function getLeafText(node) {
    console.log("getLeafText function")
    console.log(node);
    var name = node["name"];
    console.log(node);
    var bounds = getIndices(name);

    var start_index = bounds[0];
    var end_index = bounds[1];

    return getLeafTextIndices(start_index, end_index)
}

function removeSpaceBefore(s, punct) {
    for (var p in punct) {
        p = punct[p];
        s = s.split(" " + p).join(p);
        console.log(p)
    }
    return s;
}

