// Find the leaves that can serve as root sentence
function identifyCollapsible() {
    var collapsibleRanges = [];
    var max = 0;

    for (var leaf in leaves) {
        var leafID = leaf;
        leaf = leaves[leaf];
        console.log("leaf is");
        console.log(leaf);

        // Check if nucleus and is to the LEFT (for now)
        // TODO: check if we want to do right nuclei
        var isNucleus = leaf["name"].includes("Nucleus") && leaf["sibling"] > leaf["id"];
        // console.log(isNucleus);

        if (isNucleus) {
            sibling = node_map[leaf["sibling"]];
            // console.log("node is");
            // console.log(leaf);
            // console.log(sibling);
            var spanSib = (sibling["isLeaf"] == 0);
            if(spanSib) {
                // console.log(leaf);
                var tempIndex = sibling["name"].indexOf("rel2par");
                var siblingFunction = sibling["name"].substring(tempIndex+8,sibling["name"].length-1);
                // console.log("sibling is has size " + sibling["size"] + " with function " + siblingFunction);
                // console.log(sibling);

                if (sibling["size"] < elabSize && sibling["size"] > elabMin) {
                    console.log(leaf);
                    var leafNum = getLeafNumber(leaf["name"]);

                    console.log(leaf["text"]);
                    console.log(sibling);
                    var siblingText = getLeafText(sibling);
                    var bounds = getIndices(sibling.name);
                    var collapsibleRange = [leafNum, bounds[1]];
                    console.log(collapsibleRange);

                    var collapseInfo = {};

                    // Get rid of weird edge cases
                    if (collapsibleRange[0] > max && leaf["text"].split(" ").length > 2) {
                        collapseInfo["range"] = collapsibleRange;
                        collapseInfo["function"] = siblingFunction;

                        // Remove punctuation is:
                        // .replace(/\s+(\W)/g, "$1");


                        collapseInfo["headerText"] = removeSpaceBefore(leaf["text"], [".", ",", ";"]);
                        collapseInfo["bodyText"] = removeSpaceBefore(getLeafText(sibling), [".", ",", ";"]);
                        collapseInfo["isCollapse"] = 1;



                        // When there are leaves in between
                        if (collapsibleRange[0] - max > 1 && getLeafTextIndices(max+1, collapsibleRange[0]-1).replace(/\s+(\W)/g, "$1").split(" ").length >2) {
                            var leafRange = {};
                            leafRange["isCollapse"] = 0;
                            console.log(max+1, collapsibleRange[0]-1);
                            leafRange["range"] = [max+1, collapsibleRange[0] - 1];
                            leafRange["text"] = removeSpaceBefore(getLeafTextIndices(max+1, collapsibleRange[0]-1), [".", ",", ";"]);

                            console.log("og text:" + leafRange["text"]);
                            var splitPara = splitParagraphText(max+1, collapsibleRange[0]-1, mean, smallestSize, largestSize);
                            // console.log(splitPara);
                            collapsibleRanges = collapsibleRanges.concat(splitPara);

                            // collapsibleRanges.push(leafRange);
                        }
                        console.log("Collapse info");
                        console.log(collapseInfo);
                        collapsibleRanges.push(collapseInfo);


                        max = collapsibleRange[1];
                    }
                }
            }
        }
    }
    console.log(collapsibleRanges);


    // Final edge case in which the remaining text is not part of a collapsible at the very end
    // Process the remaining
    var final_range = collapsibleRanges[collapsibleRanges.length - 1]["range"];
    console.log("final range");
    var numLeaves = Object.keys(leaves_with_id).length
    if (final_range[1] < numLeaves) {
        console.log("Final concat is happening")
        var finalSplit = splitParagraphText(final_range[1]+1, numLeaves, mean, smallestSize, largestSize);
        console.log(collapsibleRanges);
        // collapsibleRanges.push(finalSplit);

        for (var s in finalSplit) {
            collapsibleRanges.push(finalSplit[s]);
        }

        console.log(collapsibleRanges);
        console.log("additinal");
        console.log(finalSplit);
    }
    console.log(collapsibleRanges);

    return collapsibleRanges
}

