// Philosophy one
function mcat1() {
    return(
        <div>
            <p><h1>Passage 1</h1></p>
            <p><div>The doubleness of the epigraphs, and the contrast between them-indeed, the contrast which Ivy McKenzie draws between the physician and the naturalist-corresponds to a certain doubleness in me: that I feel myself a naturalist and a physician both; and that I am equally interested in diseases and people; perhaps, too, that I am equally, if inadequately, a theorist and dramatist, am equally drawn to the scientific and the romantic, and continually see both in the human condition, not least in that quintessential human condition of sickness-animals get diseases, but only man falls radically into sickness.</div></p>
            <p><div>My work, my life, is all with the sick-but the sick and their sickness drives me to thoughts which, perhaps, I might otherwise not have. So much so that I am compelled to ask, with Nietzsche: As for sickness: are we not almost tempted to ask whether we could get along without it?-and to see the questions it raises as fundamental in nature. Constantly my patients drive me to question, and constantly my questions drive me to patients-thus in the stories or studies which follow there is a continual movement from one to the other.</div></p>

            <p><div>Studies, yes; why stories, or cases? Hippocrates introduced the historical conception of disease, the idea that diseases have a course, from their first intimations to their climax or crisis, and thence to their happy or fatal resolution. Hippocrates thus introduced the case history, a description, or depiction, of the natural history of disease-precisely expressed by the old word pathology. Such histories are a form of natural history-but they tell us nothing about the individual and his history; they convey nothing of the person, and the experience of the person, as he faces, and struggles to survive, his disease. There is no subject in a narrow case history; modern case histories allude to the subject in a cursory phrase (a trisomic albino female of 21), which could as well apply to a rat as a human being. To restore the human subject at the centre-the suffering, afflicted, fighting, human subject-we must deepen a case history to a narrative or tale; only then do we have a who as well as a what, a real person, a patient, in relation to disease-in relation to the physical.</div></p>

            <p><div>The patient's essential being is very relevant in the higher reaches of neurology, and in psychology; for here the patient's personhood is essentially involved, and the study of disease and of identity cannot be disjoined. Such disorders, and their depiction and study, indeed entail a new discipline, which we may call the neurology of identity, for it deals with the neural foundations of the self, the age-old problem of mind and brain. It is possible that there must, of necessity, be a gulf, a gulf of category, between the psychical and the physical; but studies and stories pertaining simultaneously and inseparably to both-and it is these which especially fascinate me, and which (on the whole) I present here-may nonetheless serve to bring them nearer, to bring us to the very intersection of mechanism and life, to the relation of psychological processes to biography.</div></p>

            <p><div> The tradition of richly human clinical tales reached a high point in the nineteenth century, and then declined, with the advent of an impersonal neurological science. Luria wrote: The power to describe, which was so common to the great nineteenth-century neurologists and psychiatrists, is almost gone now. It must be revived. His own late works, such as the Mind of a Mnemonist and The Man with a Shattered World, are attempts to revive this lost tradition. Thus the case-histories in this book hark back to an ancient tradition: to the nineteenth-century tradition of which Luria speaks; to the tradition of the first medical historian, Hippocrates; and to that universal and prehistorical tradition by which patients have always told their stories to doctors.</div></p>
        </div>)
}

// Happy american
function mcat2() {
    return (
        <div>
            <div><h1>The Happy American</h1></div>
            <div><p>Americans are a positive people. This is their reputation as well as their self-image. In the well-worn stereotype, they are upbeat, cheerful, and optimistic.</p></div>
            <div><p>Who would be churlish enough to challenge these happy features of the American personality? Take the business of positive affect, which refers to the mood they display to others through their smiles, their greetings, their professions of confidence and optimism. Scientists have found that the mere act of smiling can generate positive feelings within us, at least if the smile is not forced. In addition, recent studies show that happy feelings flit easily through social networks, so that one person's good fortune can brighten the day even for only distantly connected others. Furthermore, psychologists agree that positive feelings can actually lengthen our lives and improve our health. People who report having positive feelings are more likely to participate in a rich social life, and social connectedness turns out to be an important defense against depression, which is a known risk factor for many physical illnesses.</p></div>
            <div><p>It is a sign of progress, then, that economists have begun to show an interest in using happiness rather than just the gross national product as a measure of an economy's success. Happiness is, of course, a slippery thing to measure or define. Philosophers have debated what it is for centuries, and even if they were to define it simply as a greater frequency of positive feelings than negative ones, when they ask people if they are happy, they are asking them to arrive at some sort of average over many moods and moments.</p></div>
            <div><p>Surprisingly, when psychologists measure the relative happiness of nations, they routinely find that Americans are not, even in prosperous times and despite their vaunted positivity, very happy at all. A recent meta-analysis of over a hundred studies of self-reported happiness worldwide found Americans ranking only twenty-third. Americans account for two-thirds of the global market for antidepressants, which happen also to be the most commonly prescribed drugs in the United States.</p></div>
            <div><p>How can Americans be so surpassingly positive in self-image and stereotype without being the world's happiest and best-off people? The answer is that positivity is not so much their condition as it is part of their ideology-the way they explain the world and think they ought to function within it. That ideology is positive thinking, by which they usually mean two things. One is the generic content of positive thinking-that is, the positive thought itself-which can be summarized as Things are pretty good right now, at least if you are willing to see silver linings, make lemonade out of lemons, etc., and things are going to get a whole lot better.</p></div>
            <div><p>The second thing they mean by positive thinking is this practice of trying to think in a positive way. There is, they are told, a practical reason for undertaking this effort: positive thinking supposedly not only makes us feel optimistic but actually makes happy outcomes more likely. How can the mere process of thinking do this? In the rational explanation that many psychologists would offer today, optimism improves health, personal efficacy, confidence, and resilience, making it easier for us to accomplish our goals. A far less rational theory also runs rampant in American ideology-the idea that our thoughts can, in some mysterious way, directly affect the physical world. Negative thoughts somehow produce negative outcomes, while positive thoughts realize themselves in the form of health, prosperity, and success. For both rational and mystical reasons, then, the effort of positive thinking is said to be well worth our time and attention.</p></div>
        </div>);
}

