class InstructionSteps extends React.Component {
    render() {
        return(
            <div>
            <h1>RAR: RST Augmented Reader</h1>
                <div>
                    <p>RAR is an online text reader that leverages <b>Rhetorical Structure Theory (RST)</b> to augment reading comprehension.
                RST is a linguistic theory that explains coherence (what makes sentences actually have meaning) by postulating a
                        hierachical, connected, text structure (<a href="https://www.sfu.ca/~mtaboada/docs/publications/Taboada_Mann_RST_Part1.pdf"><i>Mann, 2006</i></a>).
                    RST postulates that all meaningful text can be represented by a tree structure in which sentences are assigned a specific function in relation
                    to the rest of the text. An example of such a function could be 'Elaboration' or 'Contrast'.
                        For more information, read about RST <a href="https://en.wikipedia.org/wiki/Rhetorical_structure_theory">here</a>.

                    <img src={'https://www.researchgate.net/profile/Pascal_Kuyten/publication/262154724/figure/fig1/AS:296671394189317@1447743380693/RST-tree-representing-the-rhetorical-structure-of-text-leaves-represent-elementary.png'} width={'102%'}></img>

                </p>
                </div>
                <div>
                    <p>We hypothesize that: <b>if readers could easily visualize and understand the underlying rhetorical relations of a text, then it would be easier to understand.</b> RAR is a platform that dynamically generates and displays
                    text based on its RST tree.
                    </p>

                </div>
                {/*<div></div>*/}
                <h1>How it works
                </h1>

                {/*Include once written out*/}


                <Steps direction="vertical" size={50}>
                <Step title="Tree Generation" description={tree_gen_instruction} status={"process"}/>
                <Step title="Topic Grouping" description={topic_grouping} status={"process"}/>
                <ParagraphText text={"Try hovering over this example text. Multiple sentences will usually be grouped together."} function={"Text function will be shown here."}/>
                <Step id="third" title="Topic Sentence Identification" description={topic_sentence} status={"process"}/>

                    <CollapsibleSentence className={"collapse-text-Elaboration"} header={"Key topic sentence will be shown here. Click on this header to expand the body text. If you hover on the header it will show the relationship the child node has to the topic sentence."} bodytext={"The extra body text will be shown here. Note that these collapsibles are color coded by function."}
                                         tooltip={"ExampleFunction"}/>
                </Steps>
                {/*<p><a onClick={renderApp}> Ready for a demo? Click here to begin. </a></p>*/}
                <p>Ready for a demo? Select the demo tab on the tap navigation bar to begin.</p>

            </div>

        );
    }
}

