// Collapsible with custom inputs
// header: Header text
// bodytext: Body text
// tooltip: Tooltip
// id: id
// Can add class if we want
class CollapsibleSentence extends React.Component {
    render() {
        return (
            <Tooltip placement={"right"} title={this.props.tooltip} visible={true}>
                <div>
                    <Collapse bordered={false} defaultActiveKey={['1']} >
                        <Panel className={"collapse-text-"+this.props.tooltip} id={this.props.id} header={<p> {this.props.header} </p>} key="1" showArrow={false}>
                            <p >{this.props.bodytext}</p>
                        </Panel>
                    </Collapse>
                </div>
            </Tooltip>
        )
    }
}

// Standard paragraph text
// text: what you want it to say
// classname:
class ParagraphText extends React.Component {
    render(x) {
        return (
            <Tooltip placement={"right"} title={this.props.function} visible={true}>
            <p style={this.props.style} className={this.props.classname + " dynamicText"}> {this.props.text} </p>
            </Tooltip>
        )
    }
}

// have list input:


const App = () => {
    // Divs to output
    const divs = [<h1>{articleTitle}</h1>];

    for (const col of collapsibles) {

        if (col["isCollapse"] == 1) {
            divs.push(<CollapsibleSentence header={col["headerText"]} bodytext={col["bodyText"]}
                                           tooltip={col["function"]}/>)
        }

        // Append regular paragraph
        else {
            divs.push(<ParagraphText text={col["text"]} function={col["function"]}/>)
        }

        // divs.push(<NavMenu/>)
    }

    // Test header
    return(
        <div>
            {divs}
        </div>
    )


};

