var node_map = {};
var counter = 1;
var leaves = {};
var leaves_with_id = {};
var leaf_counter = 1;

function traverse(o) {
    for (var i in o) {
        if (!!o[i] && typeof(o[i])=="object" ) {
            if(i != "children") {
                // continue;
                // console.log(o[i]);
                node_map[counter] = o[i];

                if(o[i]["isLeaf"]) {
                    leaves[counter] = o[i];
                    leaves_with_id[leaf_counter] = o[i];
                    leaf_counter += 1;
                }
                counter += 1;

            }
            // i == children, o is a parent object
            else {
                // for (var temp in o[i]) {
                //     // o[i][temp]["parent"] = o;
                // }
                // console.log("i is " +i);
                // console.log("o is");
                // console.log(o);

                // Store both parents and siblings of each node
                o["children"][0]["parent"] = o["id"];
                o["children"][0]["sibling"] = o["children"][1]["id"];

                o["children"][1]["parent"] = o["id"];
                o["children"][1]["sibling"] = o["children"][0]["id"];

                console.log("else case");
                console.log(o["children"][0]);
            }

            // console.log(i, o[i]);
            // console.log("i is " + i);
            // console.log(o[i].length);
            traverse(o[i]);
        }
    }
    return node_map;
}
